import Vue from 'vue'
import VueRouter from 'vue-router'
import ProductionOptimization from '../views/ProductionOptimization.vue'
import MachineLearning from '../views/MachineLearning.vue'
import DataVisualization from '../views/DataVisualization.vue'
import HomePage from '../views/HomePage.vue'
import ContactMe from '../views/ContactMe.vue'
import MyCV from '../views/About.vue'
import ToolsPage from '../views/ToolsPage.vue'
import BlogPage from '../views/BlogPage.vue'
import UnitConverter from '../views/UnitConverter.vue'
import TwoPhaseFlash from '../views/TwoPhaseFlash.vue'
import FlashCalculationPure from '../views/blog/FlashCalculationPure.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/production-optimization',
    name: 'ProductionOptimizationPage',
    component: ProductionOptimization,
  },
  {
    path: '/machine-learning',
    name: 'MachineLearningPage',
    component: MachineLearning,
  },
  {
    path: '/data-visualization',
    name: 'DataVisualizationPage',
    component: DataVisualization,
  },
  {
    path: '/',
    name: 'HomePage',
    component: HomePage,
  },
  {
    path: '/contact-me',
    name: 'ContatcMePage',
    component: ContactMe,
  },
  {
    path: '/about',
    name: 'AboutPage',
    component: MyCV,
  },
  {
    path: '/tools',
    name: 'ToolsPage',
    component: ToolsPage,
  },
  {
    path: '/blog',
    name: 'BlogPage',
    component: BlogPage,
  },
  {
    path: '/tools/unit-converter',
    name: 'UnitConverter',
    component: UnitConverter,
  },
  {
    path: '/tools/two-phase-flash',
    name: 'TwoPhaseFlash',
    component: TwoPhaseFlash,
  },
  {
    path: '/blog/flash-calculation-pure',
    name: 'FlashCalculationPure',
    component: FlashCalculationPure,
  },
]
// route level code-splitting
// this generates a separate chunk (about.[hash].js) for this route
// which is lazy-loaded when the route is visited.

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
