<template>
  <v-container
    fill-height
    fluid
    align="center"
    justify="center"
    class="mx-auto my-10"
  >
    <v-card color="rgb(230, 230, 230)" fill-height fluid class="mx-auto">
      <v-form>
        <v-card-text align="center" justify="center" class="my-auto">
          <v-row class="mx-auto my-0">
            <v-col>
              <v-autocomplete
                v-model="selected_quantity"
                :items="quantities"
                @change="onChange"
                filled
                background-color="cyan"
                dense
              ></v-autocomplete>
            </v-col>
          </v-row>
          <v-row class="mx-auto my-0">
            <v-col>
              <v-text-field
                v-model="input_value"
                type="number"
                class="centered-input text--darken-3"
                filled
                background-color="white"
                dense
              >
                <template v-slot:append>
                  <div
                    class="my-1"
                    v-html="
                      items_input.filter(
                        (item) =>
                          item.group == selected_quantity &&
                          item.name == value_asked
                      )[0].symbol
                    "
                  >
                    kg
                  </div>
                </template>

                <!-- <span
                      v-html="
                        items_input.filter(
                          (item) =>
                            item.group == selected_quantity &&
                            item.name == value_asked
                        )[0].symbol
                      "
                    ></span -->
                ></v-text-field
              >
            </v-col>
            <v-col>
              <v-autocomplete
                v-model="value_asked"
                :items="
                  items_input.filter((item) => item.group == selected_quantity)
                "
                item-text="completeName"
                item-value="name"
                filled
                background-color="blue"
                dense
              >
              </v-autocomplete>
            </v-col>
          </v-row>
          <v-row class="mx-auto my-0">
            <v-col>
              <v-text-field
                v-model="calculatedValue"
                ref="textToCopy"
                readonly
                type="number"
                class="centered-input text--darken-3"
                filled
                background-color="gray"
                dense
              >
                <template v-slot:append>
                  <div
                    class="my-1"
                    v-html="
                      items_input.filter(
                        (item) =>
                          item.group == selected_quantity &&
                          item.name == value_target
                      )[0].symbol
                    "
                  >
                    kg
                  </div>
                </template>
              </v-text-field>
            </v-col>
            <v-col>
              <v-autocomplete
                v-model="value_target"
                :items="
                  items_input.filter((item) => item.group == selected_quantity)
                "
                item-text="completeName"
                item-value="name"
                auto-select-first
                filled
                background-color="blue"
                dense
              >
              </v-autocomplete>
            </v-col>
          </v-row>

          <v-row class="my-5" align="center" justify="center">
            <v-btn color="primary" @click="copyText">copy</v-btn>
          </v-row>
        </v-card-text>
      </v-form>
    </v-card>
  </v-container>
</template>

